import React from "react";
//import ReactDOM from "react-dom";
import './Intro.scss';
import Image from 'react-bootstrap/Image'

function Intro(){
    return(
        <div className="body">
            <h2 className = "intro name-intro">Mark Thibodeaux</h2>
            <p className = "intro">Hi, I'm Mark Thibodeaux! I'm a new graduate with a major in Computer Science. During my time at school and internships I have developed both my 
                technical and communication skills to use in a development team. I'm always up for a challenge and I love learning new things. I'm excited 
                to begin my journey as a software developer and see where my career will take me! </p>

            <div className="circular--portrait">
                <Image className="pic" src={require('../../img/headshot.jpg')} alt ="headshot" roundedCircle/>
            </div>
        </div>
    )
}

export default Intro;